body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.grow img{
transition: 1s ease;
}

.grow img:hover{
-webkit-transform: scale(1);
-ms-transform: scale(1);
transform: scale(1.2);
transition: 1s ease;
}

.hover :active{
  opacity: 5;
  size:500px
}
.gutter-example .ant-row > div {
  background: transparent;
  border: 0;
}
.gutter-box {
  background: #00A0E9;
  padding: 5px 0;
}
#triangle-bottomright {
  width: 60px;
  height: 0;
  border-bottom: 800px solid #b5dae0;
  border-left: 1000px solid transparent;
  position: relative;
  z-index: 0;
  float: right;
}
#img-panda{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.grow1 figure:hover img {
	opacity: 1;
	-webkit-animation: flash 1.5s;
	animation: flash 1.5s;
}
@-webkit-keyframes flash {
	0% {
		opacity: .4;
	}
	100% {
		opacity: 1;
	}
}
@keyframes flash {
	0% {
		opacity: .4;
	}
	100% {
		opacity: 1;
	}
}
.ant-carousel .slick-slide {
  text-align: center;
  height: 550px;
  line-height: 550px;
  overflow: hidden;
}

.First {
border-radius:50px;
-moz-border-radius:50px;
-webkit-border-radius:50px;
}

.SignInBox{
  background-color: #56baed
}

#components-layout-demo-side .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

 @media screen and  (min-width: 901px) {
  .dropDownMenu{
    display: none
  }
  .welcomeFont{
    font-size: 30px;
    padding-left: 10px
  }
  .GridCol1{
    display: none 
  }
  .GridCol{
    display: block 
  }
}

 @media screen and  (max-width: 900px) {
  .icons{
    display: none
  }
  .GridCol{
    display: none 
  }
  .dropDownMenu{
    display: inline
  }
  .welcomeFont{
    font-size: 20px
  }
  .Exams{
    width: 100%
  }
} 